<template>
<div class="scrollBox">
  <div id="header-nav" class="text-center">
    <div class="left" @click="closePage">
      <div class="closeBtn"></div>
    </div>
    {{title}}
  </div>
  <div class="common-class color6B" id="commonClass" ref="comonClass">
    <div class="channel_body">
      <div class="my_channel channel">
        <div class="channel_title">
          <span class="my_title_channel">我的频道</span>
          <span class="my_title_tip">点击删除频道</span>
        </div>
        <draggable class="channel_content no-touch-bg" @start="drag=true" @end="drag=false" v-model="commonState.my_channel" :move='setMove' @update='updateFn' :options="{group:'people'}">
          <div class="channel_button_box" v-for="(item,index) in commonState.my_channel" :key="index">
            <div :class="`channel_button ${index === 0?'dragDisabled' : ''}`">
              {{item.name}}
              <div class="deleteIcon" v-if="index > 0" @click="deleteChannel(index,1)"></div>
              <!-- <img class="deleteIcon" v-if="index > 0"  @click="deleteChannel(index,1)" src="../../assets/img/delete.png"/> -->
            </div>
          </div>
          <!-- <div class="slot" slot="header">推荐</div> -->
        </draggable>
      </div>
      <div class="recomd_channel channel">
        <div class="channel_title">
          <span class="my_title_channel">推荐频道</span>
          <span class="my_title_tip" v-if="!isFull">点击添加频道</span>
          <span class="danger_tip" v-else>最多16个频道，请删除一些</span>
        </div>
        <div class="channel_content">
          <div class="channel_button_box" v-for="(item, index) in commonState.recomd_channel" :key="index">
            <div :class="`channel_button ${index === activeIndex1?'active' : ''}`" @click="addChannel(index, 2)">
              {{item.name}}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
 
</template>
<script>
import  { mapState, mapMutations } from 'vuex'
import HeaderNav from '@/components/common/header-nav'
import draggable from 'vuedraggable'
export default {
  components: {
    HeaderNav,
    draggable
  },
  data () {
    return {
      msg: '频道管理',
      isCurrentPage: true, // 传给标题栏
      activeIndex: -1, // 推荐设置点击样式
      activeIndex1: -1, // 我的频道设置点击样式
      isFull: false, // 是否频道满了
      title: '频道管理'
    }
  },
  methods: {
    ...mapMutations([
      'common_set_channel'
    ]),
    closePage () {
      // this.$emit('closePage')
      // this.$router.push('/')
      this.$router.go(-1)
    },
    /* @params index 数组下标
    @params flag true按下 false拿起
    @params type 1我的频道 2推荐 */
    addChannel (index, type) {
      if (this.commonState.my_channel.length >= 16) {
        this.isFull = true
        return false
      } else {
        this.isFull = false
      }
      this.common_set_channel({index, type})
    },
    updateFn () {
      this.$utils.cache('my_channel', JSON.stringify(this.commonState.my_channel))
      if (this.commonState.login_bol) {
        let data = {}
        let arr = []
        for (let v of this.commonState.my_channel) {
          if (v.id !== 0) {
            arr.push(v.id)
          }
        }
        data.channel_id = arr.join(',')
        // this.$api.post('CHANNEL_ORDER', data)
        this.$api.post('ALL_SET', data)
      }
    },
    setMove (e) {
      if (e.draggedContext.element.name === '推荐') {
        return false
      } else if (e.relatedContext.index === 0) {
        return false
      } else {
        return true
      }
    },
    deleteChannel (index, type) {
      if (this.commonState.my_channel.length <= 16) {
        this.isFull = false
      } else {
        this.isFull = true
      }
      this.common_set_channel({index, type})
    }
  },
  computed: mapState({
    commonState: state => state.common
  }),
  mounted () {
   
  }
}
</script>
<style lang="scss" scoped>
.scrollBox {
  #header-nav{
    position: absolute;
    z-index: 1;
    top: 0;
    width: 100%;
    height: 88px;
    font-size: 34px;
    line-height: 88px;
    background-color: #Fa0;
    align-content: center;
    color: #fff;
    .left{
      position: absolute;
      height: 100%;
      height: 100%;
      padding-left: 30px;
      display: flex;
      align-items: center;
      img{
        width: 100%;
        height: 100%;
      }
      .closeBtn{
        width: 32px;
        height: 32px;
        background: url('../../assets/img/close.png') no-repeat left top;
        background-size: 100% 100%;
      }
    }
  }
}
  #commonClass{
    font-family: PingFangSC;
    font-size: 30px;
    color:#222;
    height: 100%;
    overflow: auto;
    background: #fff;
    .channel_body {
      padding:88px 0 0;
      .channel_title {
        padding: 0 20px;
      }
      .channel_content{
        display: flex;
        flex-flow: row wrap;
        justify-content: flex-start;
        padding: 0 10px;
        margin-top: 30px;
        user-select: none;
        .channel_button_box{
          width: 25%;
          box-sizing: border-box;
          padding: 0 10px;
          background: none;
        }
        .channel_button{
          display: flex;
          align-content: center;
          border-radius:10px;
          margin-bottom: 20px;
          align-items: center;
          justify-content: center;
          margin-right: 20px;
          position: relative;
          width: 162px;
          height: 84px;
          background: #f1f5f4;
          transition: all 200ms;
          .deleteIcon{
            width: 48px;
            height: 48px;
            position: absolute;
            right: -14px;
            background: url('../../assets/img/delete.png') no-repeat left top;
            background-size: 100% 100%;
            top: -14px;
          }
        }
        .channel_button.active{
          background: #fff!important;
          box-shadow: 0px 0px 5px rgba($color: #999, $alpha: 0.6) inset;
        }
        .channel_button:nth-child(4n) {
          margin-right: 0;
        }
        .dragDisabled.channel_button{
          // background: rgb(235, 235, 228)
          color: #8a8c8b
        }
       
      }
      .recomd_channel{
        .channel_content{
          .channel_button{
            background: #fff;
            box-shadow: 0px 0px 5px rgba($color: #999, $alpha: 0.6)
          }
          .channel_button.active{
            background: #f1f5f4!important;
          }
        }
      }
      
      .channel{
        padding-top: 50px;
        .my_title_channel{
          font-size: 30px;
          color:#222;
        }
        .danger_tip{
          color: red;
          font-size: 24px;
        }
        .my_title_tip{
          color: rgb(204,204,204);
          font-size: 24px;
        }

      }
    }
   
  }
</style>



